body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(191, 238, 233);
  color: rgb(0, 0, 0);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



#ChronoBox{
  display: flex;
  width: 100%;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  font-size: 2.3rem;
}

#ChronoImage{
    height: 15%;
    width: 40%;
    margin: 0.3rem;
    padding: 5%;
}

#ChronoTime, .buttons{
    background-color:rgb(0, 0, 0);
    color: white;
    border-radius: 35% / 50%;
    border-width: 0 10px;
    padding: 10px 20px;
    text-align: center;
    cursor: none;
}

#ChronoTime{
    position: absolute;
    padding: 1rem 5rem;
}

.buttonBox {
    display: flex;
    width:50%;
    justify-content: space-around;
    align-items: center;
  }
  .buttons{
    width: 70%;
    margin: 0 15px;
    cursor: pointer;
    transition: all 0.3s ease;

  }
  .buttons:hover{
    background: white;
    color: black;
  }
